@import url(https://fonts.googleapis.com/css?family=Livvic:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}

body {
  background: #212121;
  color: wheat;
  font-family: 'Livvic', sans-serif;
}

input::-webkit-input-placeholder {
  font-family: 'Livvic', sans-serif;
}

input::-moz-placeholder {
  font-family: 'Livvic', sans-serif;
}

input:-ms-input-placeholder {
  font-family: 'Livvic', sans-serif;
}

input::-ms-input-placeholder {
  font-family: 'Livvic', sans-serif;
}

input::placeholder {
  font-family: 'Livvic', sans-serif;
}

ul {
  list-style: none;
}

.container {
  width: 90%;
  max-width: 1440px;
  margin: 25px auto;
}

/* ----SEARCH BAR---- */
.search-bar {
  text-align: center;
  height: 30px;
  margin-bottom: 100px;
}

.search-bar input {
  height: 100%;
  width: 50%;
  padding: 5px;
  color: whitesmoke;
  background: transparent;
  border: none;
  border-bottom: 1px solid wheat;
}

.search-bar button {
  height: 100%;
  padding: 0 10px;
  margin-left: 5px;
  border: 0;
  background: transparent;
  color: wheat;
  cursor: pointer;
}

/* ----Gif List---- */
.gif-list {
  -webkit-columns: 200px;
     -moz-columns: 200px;
          columns: 200px;
}

.gif {
  padding: 3%;
  display: inline-block;
  margin: 10px 0;
  border: 1px solid rgba(245, 222, 179, 0.2);
}

.gif img[alt='gif'] {
  width: 200px;
  background: #ff5e62; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to bottom,
    #ff5e62,
    #ff9966
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(#ff5e62),
    to(#ff9966)
  );
  background: -webkit-linear-gradient(
    top,
    #ff5e62,
    #ff9966
  );
  background: linear-gradient(
    to bottom,
    #ff5e62,
    #ff9966
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.gif-bottom {
  display: flex;
  align-items: center;
  margin-top: 6px;
}

.gif-bottom .avatar {
  margin-right: 5%;
  border: 1px solid wheat;
  border-radius: 50%;
  width: 26px;
  height: 26px;
  background: black;
}
/* ----Loading Icon---- */
.loading {
  font-size: 5vw;
  text-align: center;
  -webkit-animation: spin 0.8s linear none infinite;
          animation: spin 0.8s linear none infinite;
  display: inline-block;
  width: 100%;
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media only screen and (max-width: 500px) {
  .search-bar input {
    width: 65%;
  }

  .gif {
    position: relative;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
}

